<template>
    <Layout class="storage">
        <Content class="items">
            <StorageItemStack
                    class="item"
                    v-for="(stack, index) of storage"
                    :key="index"
                    :index="index"
                    :item="stack.item"
                    :amount="stack.amount"
                    :does-sell-all="doesSellAll"
            />
        </Content>
        <Footer>
            <Button v-if="doesSellAll" @click="doesSellAll = false">单个出售</Button>
            <Button v-else @click="doesSellAll = true">全售模式</Button>
        </Footer>
    </Layout>
</template>

<script>
    import {Layout, Button} from 'ant-design-vue';
    import {mapState} from 'vuex';
    import StorageItemStack from "@/components/StorageItemStack";

    export default {
        name: "Storage",
        components: {StorageItemStack, Layout, Button,
            Footer: Layout.Footer,
            Content: Layout.Content,
        },
        data() {
            return {
                doesSellAll: false,
            }
        },
        computed: {
            ...mapState(['storage']),
        },
    }
</script>

<style scoped>
    .storage {
        width: 100%;
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
    }
    .items {
        height: 100%;
        overflow-y: scroll;
    }
    .item {
        margin: .5em 1em;
        padding: .5em 1em;
    }
</style>