import {genName} from "@/util/string";
import {randInt} from "@/util/number";
import {compArr, compObj} from "@/util/object";

const ITEM_TYPES = {
    PLANT: 'plant',
    SEED: 'seed',
    COIN: 'coin',
    // MUTAGEN: 'mutagen',
    // FERTILIZER: 'fertilizer',
};

const RULES = {
    [ITEM_TYPES.PLANT]: {
        name: '植物',
        onUse(plant, slot) {
            if (!slot.plant) {
                slot.plant = plant;
                return 1;
            }
            return 0;
        },
        canPile(item, another) {
            return (
                item.gene === another.gene &&
                item.status === another.status &&
                item.value === another.value &&
                item.age === another.age &&
                item.seed === another.seed &&
                item.alive === another.alive
            );
        },
        onEstimate(plant) {
            return plant.value;
        },
    },
    [ITEM_TYPES.SEED]: {
        name: '种子',
        onUse(seed, slot) {
            if (!slot.plant) {
                slot.plant = makePlant(seed.gene);
                return 1;
            }
            return 0;
        },
        canPile(item, another) {
            return compArr(item.gene, another.gene) && compObj(item, another, 'status', 'value', 'age', 'seed', 'alive');
        },
        onEstimate(seed) {
            return seed.value;
        },
    },
    [ITEM_TYPES.COIN]: {
        name: '钱币',
        canPile() {
            return true;
        },
        onEstimate() {
            return 1;
        },
    },
    // [ITEM_TYPES.MUTAGEN]: {name: '诱变剂',},
    // [ITEM_TYPES.FERTILIZER]: {name: '肥料',},
};

function makeStack(item, amount = 1) {
    return {item, amount};
}

function makeSeed(gene, value = 0) {
    return {
        type: ITEM_TYPES.SEED,
        name: 'Seed of ' + nameOf(gene),
        gene: gene,
        value: value,
    };
}

function makePlant(gene) {
    return {
        type: ITEM_TYPES.PLANT,
        name: nameOf(gene),
        gene: gene,
        status: 0,
        value: 0,
        age: 0,
        seed: 0,
        alive: true,
    };
}

function nameOf(gene) {
    return genName(gene);
}

function run(plant) {
    if (!plant.alive) {
        return;
    }
    let gene = plant.gene;
    let reg = 0;
    let jumped = false;
    for (let i = 0; i < gene.length; i++) {
        switch (gene[i]) {
            case 1: { // 计算生长
                while (i < gene.length && randInt(0x0F) <= gene[i]) {
                    plant.status++;
                }
            } break;
            case 2: { // 生成价值
                while (i < gene.length && randInt(0x0F) <= gene[i]) {
                    plant.value++;
                }
            } break;
            case 3: { // 计算结子
                if (plant.status >= 0xFF) {
                    plant.status--;
                    plant.seed++;
                }
            } break;
            case 4: reg = gene[++i] || 0; break;
            case 5: i += reg; break;
            case 6: reg = plant.status; break;
            case 7: reg = plant.age; break;
            case 8: reg = plant.seed; break;
            case 9: {
                if (!jumped) {
                    i = Math.max(0, i - (reg || 0));
                    jumped = true;
                }
            } break;
        }
    }
    // 死亡逻辑
    let i = 0;
    let limit =  0;
    while (++i < gene.length) {
        limit += gene[i];
        if (gene[i] < 0x0F / 4) {
            break;
        }
        if (randInt(plant.age) > limit) {
            plant.dead = true;
            break;
        }
    }
    if (!plant.history) {
        plant.history = [];
    }
    plant.history.push({
        age: plant.age,
        status: plant.status,
        seed: plant.seed,
        value: plant.value,
    });
    plant.age++;
}

function harvest(plant) {
    let products = [
        makeStack(makeSeed(plant.gene, Math.ceil(plant.value / plant.age * 0.5)), plant.seed),
    ];
    products.coins = plant.value;
    return products;
}

function estimate(item) {
    let rule = RULES[item.type];
    if (rule && rule.onEstimate) {
        return rule.onEstimate(item);
    }
    return null;
}

function canPile(stack1, stack2) {
    return stack1 !== null && stack1 === stack2 && stack1.type === stack2.type && RULES[stack1.type].canPile(stack1, stack2);
}

function putInto(storage, ...itemStacks) {
    for (let itemStack of itemStacks) {
        if (itemStack.amount) {
            let stack = storage.find(s => canPile(s, itemStack));
            if (stack) {
                stack.amount += itemStack.amount;
            } else {
                storage.push(itemStack);
            }
        }
    }
}

function takeFrom(storage, index, amount) {
    if (index >= 0 && index < storage.length) {
        let stack = storage[index];
        if (!amount) {
            let [ret] = storage.splice(index, 1);
            return ret;
        } else if (stack.amount >= amount) {
            stack.amount -= amount;
            if (!stack.amount) {
                let [ret] = storage.splice(index, 1);
                return ret;
            }
            return makeStack(Object.assign({}, stack.item), amount);
        }
    }
    return null
}

function load() {
    let data = JSON.parse(localStorage.getItem('n-farm-data')) || {};
    return data;
}

function save(state) {
    let data = JSON.stringify({
        coins: state.coins,
        slots: state.slots,
        storage: state.storage,
        timestamp: state.timestamp,
    });
    localStorage.setItem('n-farm-data', data);
}

export {
    nameOf,
    makeSeed,
    makeStack,
    makePlant,
    RULES,
    ITEM_TYPES,
    run,
    harvest,
    estimate,
    putInto,
    takeFrom,
    load,
    save,
};