<template>
    <div class="slots">
        <Modal
                v-model="selectingItem"
                class="item-picker-wrapper"
                title="选择物品"
                @ok="confirmSelectItem"
                @cancel="cancelSelectItem"
        >
            <div class="item-picker">
                <MiniCard
                        v-for="(stack, index) of storage"
                        :class="(selectedStorageIndex === index) ? 'selected' : ''"
                        :key="index"
                        @click="pickItem(index)"
                >
                    <p class="item-content">{{ stack.amount }} * {{ stack.item.name }}</p>
                </MiniCard>
            </div>
        </Modal>
        <div class="slot-list">
            <FarmSlot
                    class="slot"
                    v-for="slot of slots"
                    :index="slot.index"
                    :key="slot.index"
                    :plant="slot.plant"
                    @use="selectItem"
                    @show="showInfo"
            />
        </div>
        <PlantInfo class="plant-info" v-if="showPlantInfo" :plant="selectedPlant"/>
    </div>
</template>

<script>
    import {Modal} from 'ant-design-vue';
    import FarmSlot from "@/components/FarmSlot";
    import {mapState} from 'vuex';
    import MiniCard from "@/components/MiniCard";
    import PlantInfo from "@/components/PlantInfo";

    export default {
        name: "Farm",
        components: {PlantInfo, MiniCard, FarmSlot, Modal},
        data() {
            return {
                selectingItem: false,
                selectedStorageIndex: -1,
                selectedSlotIndex: -1,
                showPlantInfo: false,
            }
        },
        computed: {
            ...mapState(['slots', 'storage']),
            selectedPlant() {
                return this.slots[this.selectedSlotIndex].plant;
            },
        },
        methods: {
            selectItem(slotIndex) {
                this.selectedSlotIndex = slotIndex;
                this.selectingItem = true;
            },
            pickItem(storageIndex) {
                this.selectedStorageIndex = storageIndex;
            },
            confirmSelectItem() {
                if (this.selectedSlotIndex >= 0 && this.selectedStorageIndex >= 0) {
                    this.$store.commit('useAt', {
                        slotIndex: this.selectedSlotIndex,
                        storageIndex: this.selectedStorageIndex,
                    });
                }
                this.selectedSlotIndex = -1;
                this.selectedStorageIndex = -1;
                this.selectingItem = false;
            },
            cancelSelectItem() {
                this.selectedSlotIndex = -1;
                this.selectedStorageIndex = -1;
                this.selectingItem = false;
            },
            showInfo(index) {
                this.selectedSlotIndex = index;
                this.showPlantInfo = this.selectedSlotIndex >= 0 && !!this.slots[this.selectedSlotIndex].plant;
            },
        },
    }
</script>

<style scoped>
    .slots {
        height: 100%;
        overflow-x: hidden;
        overflow-y: hidden;
        display: flex;
        flex-direction: column;
    }

    .slot-list {
        overflow-y: auto;
        flex: 1;
    }

    .plant-info {
        width: 100%;
        overflow-x: hidden;
    }

    .slot {
        margin: .5em 1em;
        padding: .5em 1em;
    }
    .item-picker {
        height: 100%;
        padding: 1em;
    }
    .item-picker > * {
        margin: .5em 0 ;
    }
    .item-content {
        margin: 0;
    }
    .selected {
        border: .1em solid #7eb2e6;
    }
</style>