<template>
    <div class="mini-card"
         @click="transferEvent('click', $event)"
         @mouseenter="transferEvent('mouseenter', $event)"
         @mouseleave="transferEvent('mouseleave', $event)"
    >
        <slot />
    </div>
</template>

<script>
    export default {
        name: "MiniCard",
        methods: {
            transferEvent(type, event) {
                this.$emit(type, event);
            },
        },
    }
</script>

<style scoped>

    .mini-card {
        padding: .6em 1em;
        background-color: #ffffff;
        border-radius: .2em;
        border: 1px solid #E8E8E8;
    }
</style>