import Vue from 'vue';
import App from './App.vue';
import Vuex from 'vuex';
import st from './store';


import {message} from 'ant-design-vue'
Vue.prototype.$message = message;
message.config({
  duration: 1,
});
Vue.use(Vuex);

Vue.config.productionTip = false;

const store = new Vuex.Store(st);

new Vue({
  render: h => h(App),
  store,
}).$mount('#app')
