<template>
    <MiniCard class="farm-slot" @click="focus = true" @mouseleave="focus = false">
        <div class="plant-display">
            <div v-if="plant">
                <p>{{ text }}</p>
                <Progress strokeLinecap="square" color="#66ccff" :percent="plant.status / 0xFF * 100" :format="percent => percent.toFixed(2) + '%'"/>
            </div>
            <p v-else>空</p>
        </div>
        <ButtonGroup v-if="focus">
            <Button @click="harvest(index)">收获</Button>
            <Button @click="collect(index)">回收</Button>
            <Button @click="use">使用</Button>
            <Button @click="show">信息</Button>
        </ButtonGroup>
    </MiniCard>
</template>

<script>
    import {Button, Progress} from 'ant-design-vue';
    import {mapMutations} from 'vuex';
    import MiniCard from "@/components/MiniCard";

    export default {
        name: "FarmSlot",
        components: {
            MiniCard, Button, Progress,
            ButtonGroup: Button.Group,
        },
        props: {
            index: {
                type: Number,
                required: true,
            },
            plant: {
                type: Object,
                default: () => null,
            },
        },
        data() {
            return {
                focus: false,
            };
        },
        computed: {
            text() {
                const p = this.plant;
                return `${p.name}-${p.alive ? 'A' : 'D'}-T${p.status}-S${p.seed}-V${p.value}`;
            },
        },
        methods: {
            use() {
                this.$emit('use', this.index);
            },
            show() {
                this.$emit('show', this.index);
            },
            ...mapMutations(['harvest', 'collect']),
        },
    }
</script>

<style scoped>
    .farm-slot {
        margin: 0;
        padding: 0;
    }
    .plant-display > * {
        width: 100%;
        margin: 0;
        padding: .5em 1.5em;
        text-align: left;
        font-family: Consolas, sans-serif;
    }
</style>