import {
    estimate,
    harvest,
    load,
    makeSeed,
    makeStack,
    putInto,
    RULES,
    run,
    save,
    takeFrom
} from "@/model/plant";
import {randArr} from "@/util/number";


export default {
    state: {
        coins: 5000,
        slots: [],
        storage: [],
        timestamp: Date.now(),
        pid: null,
    },
    mutations: {
        harvest(state, index) {
            let plant = state.slots[index].plant;
            if (plant) {
                state.slots[index].plant = null;
                let products = harvest(plant);
                putInto(state.storage, ...products);
                state.coins += products.coins || 0;
            }
            save(state);
        },
        collect(state, index) {
            let plant = state.slots[index].plant;
            if (plant) {
                state.slots[index].plant = null;
                putInto(state.storage, makeStack(plant));
            }
            save(state);
        },
        sell(state, index) {
            let stack = takeFrom(state.storage, index, 1);
            if (stack) {
                let value = estimate(stack.item);
                if (value !== null) {
                    state.coins += value;
                } else {
                    putInto(state.storage, stack);
                }
            }
            save(state);
        },
        sellAll(state, index) {
            let stack = takeFrom(state.storage, index);
            if (stack) {
                let value = estimate(stack.item);
                if (value !== null) {
                    state.coins += value * stack.amount;
                } else {
                    putInto(state.storage, stack);
                }
            }
            save(state);
        },
        buySeed(state, option = 'normal') {
            switch (option) {
                case "normal": if (state.coins < 100) {return;} state.coins -= 100; break;
                case "long-gene": if (state.coins < 400) {return;} state.coins -= 400; break;
                case "high-value": if (state.coins < 200) {return;} state.coins -= 200; break;
            }
            let seed;
            switch (option) {
                case "normal": seed = makeSeed(randArr(100, 50, 0x0F, 0), 25); break;
                case "long-gene": seed = makeSeed(randArr(200, 150, 0x0F, 0), 100); break;
                case "high-value": seed = makeSeed(randArr(150, 100, 0x2F, 0), 50); break;
            }
            putInto(state.storage, makeStack(seed));
            save(state);
        },
        buySlot(state) {
            if (state.coins >= 1000) {
                state.coins -= 1000;
                state.slots.push({index: state.slots.length, plant: null});
                return true;
            }
            save(state);
        },
        useAt(state, {slotIndex, storageIndex}) {
            let slot = state.slots[slotIndex];
            let stack = state.storage[storageIndex];
            let rule = RULES[stack.item.type];
            if (rule && rule.onUse) {
                let cost = rule.onUse(stack.item, slot);
                if (cost) {
                    takeFrom(state.storage, storageIndex, cost);
                }
             }
            save(state);
        },
        tick(state, dt) {
            const slots = state.slots;
            for (let i = 0; i < dt; i++) {
                slots.forEach(slot => {
                    if (slot.plant) {
                        run(slot.plant)
                    }
                });
            }
            state.timestamp = Date.now();
            save(state);
        },
        setPid(state, pid) {
            state.pid = pid;
        },
        load(state) {
            let data = load();
            Object.assign(state, data);
        },
        save(state) {
            save(state);
        },
        clear(state) {
            let data = JSON.stringify({
                coins: 0,
                slots: [],
                storage: [],
            });
            Object.assign(state, data);
            localStorage.removeItem('n-farm-data');
        },
    },
    actions: {
        start({commit, state}) {
            commit('load');
            if (state.timestamp) {
                const dt = Math.floor((Date.now() - state.timestamp) / 300000);
                commit('tick', dt);
            }
            if (state.pid !== null) {
                clearInterval(state.pid);
            }
            let pid = setInterval(() => commit('tick', 1), 300000);
            commit('setPid', pid);
        },
        stop({commit, state}) {
            if (state.pid !== null) {
                clearInterval(state.pid);
                commit('setPid', null);
            }
            commit('save');
        },
    },
};