<template>
    <div>
        <div class="line"><span>名称</span><span>{{ plant.name }}</span></div>
        <div class="line"><span>年龄</span><span>{{ plant.age }}</span></div>
        <div class="line"><span>生长</span><span>{{ status }}</span></div>
        <div class="line"><span>种子</span><span>{{ plant.seed }}</span></div>
        <div class="line"><span>价值</span><span>{{ plant.value }}</span></div>
        <div class="line"><span>状态</span><span>{{ alive }}</span></div>
        <div class="line"><span>基因</span></div>
        <div class="gene">{{ gene }}</div>
        <canvas class="canvas" ref="canvas"></canvas>
    </div>
</template>

<script>
    import * as echarts from 'echarts';

    export default {
        name: "PlantInfo",
        props: {
            plant: {
                type: Object,
                required: true,
            },
        },

        data() {
            return {
                history: this.plant.history || [],
            }
        },

        computed: {
            status() {
                return (this.plant.status / 0xFF).toFixed(2) + '%';
            },
            alive() {
                return this.plant.alive ? "存活" : "死亡";
            },
            gene() {
                return this.plant.gene.map(g => g.toString(16)).join(' ');
            },
        },

        methods: {
            drawCurve() {
                const cvs = this.$refs.canvas;
                cvs.width = cvs.clientWidth;
                cvs.height = cvs.clientHeight;
                echarts.init(cvs).setOption({
                    legend: {data: ['生长', '种子', '价值']},
                    dataZoom: [{
                            id: 'dataZoomX',
                            type: 'slider',
                            xAxisIndex: [0],
                            filterMode: 'filter'
                        }, {
                            id: 'dataZoomY',
                            type: 'slider',
                            yAxisIndex: [0],
                            filterMode: 'empty'
                        }],
                    dataset: {
                        source: this.history,
                    },
                    xAxis: {type: 'value', name: '年龄'},
                    yAxis: {},
                    series: [
                        {type: 'line', name: '生长', encode: {x: 'age', y: 'status'}},
                        {type: 'line', name: '种子', encode: {x: 'age', y: 'seed'}},
                        {type: 'line', name: '价值', encode: {x: 'age', y: 'value'}},
                    ],
                });
            }
        },

        mounted() {
            this.drawCurve();
        },
    }
</script>

<style scoped>

    .line {
        margin: 0 1em;
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 1.2em;
    }

    .line > span:first-child {
        margin-right: 1em;
        font-weight: bold;
    }

    .gene {
        margin: 0 1em;
        text-align: left;
    }

    .canvas {
        width: 100%;
        height: 30vh;
    }
</style>